import Draggable from "react-draggable";
import { BigButton } from "./BigButton"; // The default
import { FaCheck, FaTimes } from 'react-icons/fa'
import { cleanBorder, errorColor, goodColor, primary45 } from "../utils/colors";

export default function DraggableSignature({ url, onEnd, onSet, onCancel }) {
  const styles = {
    container: {
      position: 'absolute',
      zIndex: 100000,
      border: `2px solid ${primary45}`,
    },
    controls: {
      position: 'absolute',
      right: 0,
      display: 'inline-block',
      backgroundColor: primary45,
      // borderRadius: 4,
    },
    smallButton: {
      display: 'inline-block',
      cursor: 'pointer',
      padding: 4,
    }
  }
  return (
    <Draggable onStop={onEnd} ok=".ok" cancel=".btn">
      <div style={styles.container} >
        <div style={styles.controls}>
          <button style={styles.smallButton} onClick={onSet} className="ok"><FaCheck color={goodColor} /></button>
          <button style={styles.smallButton} onClick={onCancel} className="btn"><FaTimes color={errorColor} /></button>
        </div>
        <img src={url} width={200} style={styles.img} draggable={false} alt="" />
      </div>
    </Draggable>
  );
}
