import { primary45 } from "./utils/colors";

export function Header() {
  const styles = {
    container: {
      backgroundColor: primary45,
      color: '#FFF',
      padding: 12,
      fontWeight: 600,
      display: "flex",
      flexDirection: " row-reverse",
    }
  }
  return <div style={styles.container}>
    <div>חתום על חוזה</div>
  </div>
}
